.player {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.player video{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: contain;
    z-index: 1;
}

.player video.inactive{
    filter: brightness(50%);
}

.player video.active{
    filter: brightness(100%);
}

.start-pause {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  color: white;
}

.fade-in {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s 0.25s, opacity 0.25s linear;
}

.fade-out {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.25s, opacity 0.25s linear;
}

.fade {
  margin-top: 25px;
  font-size: 21px;
  text-align: center;

  -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
     -moz-animation: fadein 2s; /* Firefox < 16 */
      -ms-animation: fadein 2s; /* Internet Explorer */
       -o-animation: fadein 2s; /* Opera < 12.1 */
          animation: fadein 2s;
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.control {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.start-pause i {
  font-size: 3em;
}

.start-pause i:hover {
  cursor: pointer;
}

.menu {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 32px;
  background-color: rgba(29, 29, 29);
  display: flex;
  flex-direction: row;
  z-index: 1;
}

.menu .item{
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu .item.play-pause {
  color: white;
  margin: 0 1rem 0 1rem;
}

.menu .item.play-pause i:hover {
  cursor: pointer;
}

.menu .item.seekslider{
  position: relative;
  flex: 1 1;
  padding: 0 1rem 0 0; 
}

.menu .item.time{
  color: white;
  margin: 0 1rem 0 0;
  font-family: 'Fira Mono', monospace;
  font-size: 0.9em;
}

.menu .item.expand {
  cursor: pointer;
  color: white;
  margin: 0 1rem 0 0;
}


input[type=range] {
  height: 6px;
  -webkit-appearance: none;
  width: 100%;
  background: transparent; /* Otherwise white in Chrome */
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 150px;
  height: 6px;
  cursor: pointer;
  animate: 0.2s;
  background: white;
  border-radius: 14px;
  border: 0px solid #000000;
}

input[type=range]::-webkit-slider-thumb {
  height: 6px;
  width: 10px;
  border-radius: 14px;
  background: #529DE1;
  cursor: pointer;
  -webkit-appearance: none;
}

/* input[type=range]:focus::-webkit-slider-runnable-track {
  background: #50555C;
} */

input[type=range]::-moz-range-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  animate: 0.2s;
  background: #50555C;
  border-radius: 50px;
  border: 0px solid #000000;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 18px;
  width: 50px;
  border-radius: 50px;
  background: #529DE1;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 15px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #50555C;
  border: 0px solid #000000;
  border-radius: 28px;
  box-shadow: 1px 1px 1px #50555C;
}
input[type=range]::-ms-fill-upper {
  background: #50555C;
  border: 0px solid #000000;
  border-radius: 28px;
  box-shadow: 1px 1px 1px #50555C;
}
input[type=range]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 0px 0px #000000;
  border: 0px solid #000000;
  height: 18px;
  width: 50px;
  border-radius: 50px;
  background: #529DE1;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #50555C;
}
input[type=range]:focus::-ms-fill-upper {
  background: #50555C;
}






