:root {
    --primary-color: #447fb6;
    --secondary-color: #a5c7e6;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
}

h1 {
    font-size: 2.5em;
    margin: 0;
}

h2 {
    font-size: 2em;
    margin: 0;
}

h3 {
    font-size: 1.5em;
    margin: 0;
}

header {
    width: 100%;
    height: 100vh;
    background-color: rgb(22, 22, 22);
    /* background: url(../static/dark-roz3.jpg); */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    color: white;
    position: relative;
}

.brnd {
    position: absolute;
    top: 0;
    left: 0;
}

.brnd h3 {
    margin: 2rem 0.5rem;
}

img {
    width: 100%;
    height: 100%;
}


footer {
    border-top: 1px solid black;

    background-color: #1C1C1C;
    color: white;
}

footer {
    display: flex;
    flex-direction: column;
}


footer .fc ul {
    list-style-type: none;
    font-size: 1rem;
    display: flex;
    justify-content: flex-end;
}
footer .fc ul li {
    margin-left: 1rem;
}

footer .fc ul li a{
    color: white;
    text-decoration: none;
    transition: 250ms;
}

footer .fc ul li a:hover{
    color: #447fb6;
    transition: 250ms;
}

footer .fc div i{
    margin-right: 0.5rem;
}

footer .fc ul li a i{
    margin-right: 0.5rem;
}

@media only screen and (max-width: 479){ 
    
    footer .fc ul {
        display: none;
    }
}

@media only screen and (min-width: 480px) and (max-width: 667px){ 
    .brnd h3 {
        margin: 2rem 2rem;
    }    
    
    footer {
        display: flex;
        flex-direction: row;
    }

    footer .fc {
        width: 100%;
        margin: 0.5rem 2rem;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
}

@media only screen and (min-width: 668px) and (max-width: 999px){ 
    .brnd h3 {
        margin: 2rem 3rem;
    }    
    
    footer {
        display: flex;
        flex-direction: row;
    }

    footer .fc {
        width: 100%;
        margin: 1rem 3rem;
        display: flex;
        justify-content: space-between;
    }
} 

@media only screen and (min-width: 1000px){ 
    .brnd {
        width: 100%;
    }
    .brnd h3 {        
        margin: 3rem;
    }

    footer {
        display: flex;
        align-items: center;
        flex-direction: row;
        height: 3rem;
    }

    footer .fc {
        min-width: 1000px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
} 

@media only screen and (min-width: 1500px){ 
    body {
        font-size: 1.15rem;
    }
} 


