.about {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    padding: 0 4rem;
}

.about p {
    font-size: 1em;
    line-height: 1.3rem;
    text-align: center;
    max-width: 800px;
    margin: auto;
}

.about p span {
    color: var(--primary-color);
}


@media only screen and (min-width: 480px) and (max-width: 667px){ 
    .about p {
        font-size: 1.2em;
        line-height: 1.5rem;
    }
}

@media only screen and (min-width: 668px) and (max-width: 999px){ 
    .about p {
        font-size: 1.4em;
        line-height: 1.6rem;
    }
}

@media only screen and (min-width: 1000px){ 
    .about p {
        font-size: 1.5em;
        line-height: 1.9rem;
    }
} 

@media only screen and (min-width: 1500px){ 
    .about p {
        font-size: 1.5em;
        line-height: 1.9rem;
    }
} 
