.timestamp {
    position: relative;
    background-color: rgba(29, 29, 29);
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    border-width: 1px;
    border-left-width: 0;
    margin-bottom: 0.5rem;
    text-align: right;
    vertical-align: middle;
    font-size: 0.8em;
    width: 150px;
    transition: 250ms;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    color: white;
}

.timestamps .timestamp:hover {
    cursor: pointer;
    background-color: white;
}

.timestamp span {
    text-align: left;
    vertical-align: middle;
    padding: 0.35rem 0.5rem;
    width: 150px;
    transition: 250ms;
}

.timestamp span:hover {
    color: black;
    transition: 500ms;
}

.timestamp .timer {
    height: 2px;
    background-color: white;
    border-bottom-right-radius: 4px;
}

@media (max-width: 440px){ 
    .timestamp {
        visibility: hidden;
    }
}
