.about .references {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.about .references a, .about .references i {
    width: 3rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    margin-bottom: 0.5rem;
}

.about .references a:hover, .about .references i:hover {
    cursor: pointer;
}

.about .references a i {
    font-size: 2em;
}

.about .references a {
    color: white;
}

.about ul {
    list-style-type: none;
}

.tooltip {
    position: relative;
    display: inline-block;
}
  
.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    top: 9px;
    left: 105%;
}
  
.tooltip:hover .tooltiptext {
    visibility: visible;
}

.about .references ul .email{
    display: flex;
}

.about .references ul .email i{
    font-size: 2em;
}

.about .references ul .email div{
    display: flex;
    justify-content: center;
    align-items: center;
}


