.about .references ul .linkedin > a { transition: all .2s ease-in-out; opacity:0.9; }
.about .references ul .linkedin > a:hover { transform: scale(1.2); opacity:1; color: #447fb6; }

.about .references ul .github > a  { transition: all .2s ease-in-out; opacity:0.9; }
.about .references ul .github > a:hover  { transform: scale(1.2); opacity:1; color: #447fb6; }


.about .references ul .email .tooltip > i { transition: all .2s ease-in-out; opacity:0.9; }
.about .references ul .email .tooltip > i:hover{ transform: scale(1.2); opacity:1; color: #447fb6; }



.about .references ul .email input {
    display: none;
}


